@import "../../../../styles/variables.scss";

.table-container {
  width: 900px;
  margin: auto;
  font-size: 13px;
  .ReactTable {
    padding-top: 15px;
    .rt-table {
      margin-top: 10px !important;
      .rt-thead.-header {
        .rt-tr {
          .rt-th {
            padding: 10px !important;
            color: $text-primary-color !important;
          }
        }
      }
      .rt-thead.-filters {
        .rt-tr {
          .rt-th {
            input {
              padding: 7px !important;
            }
          }
        }
      }
      .rt-tbody {
        .rt-tr-group {
          .rt-td {
            color: none !important;
          }
          .rt-td:last-child {
            // text-align: left;
            width: 50px !important;
            border-right: solid 1px #dddddd;
            padding: 10px 10px;
          }
        }
      }
    }
  }
}
